.TodoItem {
    position: relative;
    display: flex;
    max-width: 500px;
    margin: 0 auto;
    margin-top: 24px;
    justify-content: center;
    align-items: center;
    background-color: #FAFAFA;
    box-shadow: 0px 5px 50px rgba(32, 35, 41, 0.15);
}

.Icon {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 48px;
    width: 48px;
    font-size: 2.4rem;
    font-weight: bold;
    cursor: pointer;
  }
  
  .Icon-check {
    position: absolute;
    left: 12px;
  }

  .Icon-check--active {
    color: #4caf50;
  }
  
  .Icon-delete {
    position: absolute;
    top: -2px;
    right: 0;
  }
  .Icon-delete:hover {
    color: red;
  }

  .TodoItem-p {
    margin: 24px 0 24px 24px;
    width: calc(100% - 120px);
    font-size: 1.8rem;
    line-height: 2.4rem;
    font-weight: 400;
  }
  .TodoItem-p--completed {
    text-decoration: line-through;
  }